body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Karla', sans-serif;
  position: relative;
  background-color: #F5F7FB;
}

.start-questions-page{
  display: flex;
  flex-direction: column;
  text-align: center;
  align-items: center;
  justify-content: center;
  width: 50%;
  height: 100vh;
  width: 100vw;
  color: #293264;
}
.start-questions-page .title{
  font-size: 32px;
  font-weight: bold;
  margin: 0;
}
.start-questions-page .description{
  font-size: 22px;
  padding: 10px;
}
.main-btn{
  padding: 20px 30px;
  border: none;
  background-color: #4D5B9E;
  color: white;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  font-size: 18px;
  cursor: pointer;
  width: fit-content;
}
.all-questions{
  padding: 40px;
  color: #293264;
}
.all-questions >*:not(:last-child){
  padding: 20px 0;
  border-bottom: 1px solid #DBDEF0;
}
.all-questions .title{
  margin: 0;
  font-size: 16px;
  font-weight: bold;
}
.all-questions .all-answers{
  display: flex;
  align-items: center;
  gap: 20px;
}
.all-questions .all-answers >*{
  transition: all 0.3s ease;
  padding: 15px 20px;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  border: 2px solid #293264;
  color: #293264;
  cursor: pointer;
  background: none;
  font-size: 20px;
  font-weight: bold;
  -webkit-transition: all 0.3s ease;
  -moz-transition: all 0.3s ease;
  -ms-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
}
.all-questions .all-answers >*.active{
  background-color: #D6DBF5;
  border-color: #D6DBF5;
  
}
.all-questions .all-answers >*.correct{
  background-color: #94D7A2;
  border-color: #94D7A2;
  pointer-events: none;
}
.all-questions .all-answers >*.incorrect{
  background-color: #F8BCBC;
  border-color: #F8BCBC;
  pointer-events: none;
}
.all-questions .all-answers >*.not-cheacked{
  opacity: 0.4;
  pointer-events: none;
}
.result{
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 20px;
  gap: 40px;
}
.result-statis{
  font-size: 26px;
  font-weight: bold;
}
.result .main-btn{
  display: block;
  width: fit-content;
  padding: 20px 40px;
  display: block;
}
